import React from 'react';
import * as yup from "yup";

import AppContainerItem from "../common/AppContainer/AppContainerItem";
import questionImg from '../../assets/Frame26/question.svg';
import AppContainer from "../common/AppContainer/AppContainer";
import {callOrderTemplate} from "../../utils/consts";
import FeedbackForm from "../FeedbackForm/FeedbackForm";


const schema = yup.object().shape({
    username: yup.string()
        .required(' ')
        .min(3, ' 3 ')
        .max(12, ' 12 '),
    surname: yup.string()
        .required(' ')
        .min(3, ' 3 ')
        .max(12, ' 12 '),
    email: yup.string()
        .required(' ')
        .email(' email'),
    phone: yup.string()
        .required(' ')
        .matches(/^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/, ' ')
});

const NeedHelp = ({grey}) => {
    return (
        <div>
            <a name={"help"}/>
            <AppContainer title={' ?'} grey={!!grey} reverse>
                <AppContainerItem lg={7} md={7} sm={12} xs={12}>
                    <img style={{maxWidth: '100%', maxHeight: '100%',}} src={questionImg}/>
                </AppContainerItem>
                <AppContainerItem margin lg={5} md={5} sm={12} xs={12}>
                    <FeedbackForm template={callOrderTemplate} schema={schema} buttonText={' '}>
                        {[
                            {name: 'username', placeholder: '*'},
                            {name: 'surname', placeholder: '*'},
                            {name: 'email', placeholder: 'Email*'},
                            {name: 'phone', placeholder: ' *'},
                        ]}
                    </FeedbackForm>
                </AppContainerItem>
            </AppContainer>
        </div>
    );
};

export default NeedHelp;