import React from 'react';
import * as yup from "yup";
import {connect} from "react-redux";

import Form from "../common/Form/Form";
import {addNewUser} from "../../store/userReducer";


const schema = yup.object().shape({
    email: yup.string()
        .required('   ')
        .email('  email'),
    password: yup.string()
        .required('   ')
        .min(6, ' 6 ')
        .max(12, ' 12 '),
    confirmPassword: yup.string()
        .required('   ')
        .oneOf([yup.ref("password"), null], "  "),
});

const Registration = ({addNewUser, ...props}) => {
    const registration = async (data) => {
        await addNewUser(data.email, data.password)
    }

    return (
        <div>
            <Form onSubmit={registration} buttonText={''} schema={schema} {...props}>
                {[
                    {name: "email", placeholder: 'Email*', type: "email"},
                    {name: "password", placeholder: '*', type: "password"},
                    {name: "confirmPassword", placeholder: ' *', type: "password"},
                ]}
            </Form>
        </div>
    );
};

export default connect(null, {addNewUser})(Registration);

